.itemdetail * {
  /* border: 1px dotted orange; */
}

.itemdetail {
  /* width: 100vh; */
  height: 100vh;
  position: relative;
  padding-top: 55px;
  z-index: 999;
}

.itemdetail-top-area {
  display: flex;
}

.main_photo {
  flex: 1;
  height: 600px;
}
.main_photo img{
width: 100%;
}

.itemdetail__info {
  flex: 1;
  margin-left: 70px;
}

.itemdetail__title__box {
margin-bottom: 10px;
}

.itemdetail__title {
  height: 40px;
  font-size: 2.4rem;
}
.itemdetail__cont {
  letter-spacing: 0px;
  font-weight: 500;
  font-size: 13px;
  margin-top: 5px;
  margin-bottom: 40px;
  color: #8d8d8d;
}

.itemdetail__price{
  color: #1a1a1a;
  font-size: 2.3rem;

}

.itemdetail__price span{
  font-size: 2.8rem;
  font-weight: bold;
}


.itemdetail__notice__box {
  display: flex;
  align-items: center;
  padding: 20px 0;
  box-sizing: border-box;
  border-top: 1px dashed #e2e2e2;
  border-bottom: 1px dashed #e2e2e2;
}

.itemdetail__notice__left {
  flex: 1;
}

.itemdetail__notice__right {
  flex: 3;
}

.itemdetail__notice__right span:nth-child(1){
font-weight: bold;
color: #ff8400;
}

.itemdetail__notice__box span {
  font-size: 1.3rem;
  display: inline-block;
}



.itemdetail__sell__title {
  color: #333;
  font-size: 1.4rem;
  font-weight: 600;
}

.itemdetail__sell__box {
  position: relative;
  min-height: 30px;
  line-height: 30px;
  padding: 15px 0px;

  color: #333;
  font-size: 1.3rem;
  font-weight: 300;
  /* display: flex; */
}

.sell__button__box {
  position: absolute;
  width: 35px;
  height: 35px;
  padding: 0 35px;
  border: 1px solid #e6e6e6;
  background: #fff;
  font-size: 2.5rem;
  text-align: center;
  right: 126px;
  top: 13px;
}

.count__button {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 33px;
  height: 33px;
border: none;

}


.sb_down {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 33px;
  height: 33px;
  background: #fff;
  font-size: 2.2rem;
  line-height: 1.3;
  cursor: pointer;

}
.sb_total{
width: 100%;
height: 33px;
margin: 0;
padding: 0;
border: 0;
color: #232323;
font-size: 1.3rem;
line-height: 2.6;
text-align: center !important;
font-weight: bold;
}




.sb_up {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 33px;
  height: 33px;
  background: #fff;
  font-size: 2.2rem;
  line-height: 1.3;
  cursor: pointer;
}

.itemdetail__sell__price{
   right: 45px;

  top: 16px;
  color: #333;

  position: absolute;
}


.itemdetail__sell__price span{
  font-size: 1.2rem;
  font-weight: bold;
}


.itemdetail__total__box {
  padding: 20px 0;
  border-top: 1px solid #e4e4e5;
  position: relative;
}

.itemdetail__total__left {
  font-size: 1.6rem;
  font-weight: 500;
  color: #0e0e0e;
}

.itemdetail__total__price{
  position: absolute;
  right: 0;
  display: inline-block;
  margin-top: -4px;
  font-size: 2rem;
  color: #000;
  vertical-align: middle;

}

.itemdetail__total__price span{
  font-size: 2.4rem;
  font-weight: bold;
}


.itemdetail__button__box{
text-align: right;
padding-top: 30px;
}



.itemdetail__button{
width: 33%;
border: 1px solid #333;
color: #333;
background-color: #fff;
font-size: 1.5rem;
padding: 10px;
margin-left: 10px;
cursor: pointer;
}

.itemdetail__button:hover{
background-color: #efefef; 
}

