

.shop__box{
  display: flex;

  }


  .shop__list__left{
padding: 4vh 4vw 4vh 0;
    min-width: 220px;
    box-sizing: border-box;
  }
  
  .shop__list__left h2{

    font-size: 2.4rem;
    padding-bottom: 10px;
    border-bottom: 1px solid #dadada;
  }

  .shop__list__left li{
    position: relative;
    padding-top: 10px;
    font-size: 1.5rem;
  }

.shop__item__right{
  flex: 1;

}

.shop input[type="checkbox"]{
        display: none;
      }

.shop input[type="checkbox"] + label{
        display: inline-block;
        
        width: 10px;
        height: 10px;
        border:1px solid #c1c1c1;
        position: absolute;
        right: 0;

      }

.shop input:checked + label::after{
  content: 'L';
    font-size: 15px;
    width: 10px;
    height: 10px;
    text-align: center;
    position: absolute;
    left: 4px;
    top: -7px;
    transform: scalex(-1) rotate(317deg);
}




  .shop__item{
    flex: 4;
  }
  .shop__item__top{ 
    text-align: center;
    padding-top: 4vh;
    padding-bottom: 10px;
  }
  
  .shop__item__title{
    font-size: 2.8rem;
    height: 80px;
  }
 





  .shop__item__order{
    display: flex;
    width: 100%;
    height: 30px;
    justify-content: space-between;
    position: relative;
  }
  
  
  .shop__item__count{
    font-weight: bold;
    color: #111;
  }
  
  .shop__item__count span:nth-child(1){
    font-size: 1.8rem;
  
  }
  .shop__item__count span:nth-child(2){
    font-size: 1.3rem;
  
  }
  
  
  .shop__item__orderlist{
    font-size: 1.3rem;
    margin: auto 0;
  }

  .orderlist{
    display: inline;
    margin-left: 9px;
    color: #a0a0a0;
    cursor: pointer;
  }
  
  .orderlist.on{
    margin-left: 9px;
    font-weight: bold;
    color: #222;
  }
  
  
  .shop .pagination{

    text-align: center;
    padding-top: 1vh;
    padding-bottom: 5vh;
  }

  .shop .pagination span{
    cursor: pointer;
    font-size: 1.5rem;
    padding: 0 10px;
  }

  .shop .pagination span.active{
 font-weight: bold;
  }



  @media screen and (max-width:1023px){
  
    .shop__list__left {
      padding: 4vh 4vw;
    }
    .shop__item__right{
      padding: 4vh 4vw;
    }    
    
    .shop__item__order {
      justify-content: flex-end;
      flex-direction: column;
  }
  .shop__item__top {
    text-align: left;
  }
  .orderlist {
    margin-left: 0px;
    margin-right: 9px;
  }
  .orderlist.on {
    margin-left: 0px;
    margin-right: 9px;
  }
  .shop__item__orderlist {
    margin: 1vh 0 0;
  }
  }

